import axios from 'axios'

const url = 'https://vilkkoni-server.herokuapp.com/events';
const user_url = 'https://vilkkoni-server.herokuapp.com/user';
const validate_url = 'https://vilkkoni-server.herokuapp.com/validate';
const sub_url = 'https://vilkkoni-server.herokuapp.com/sub';

export const fetchEvents = () => axios.get(url);
export const fetchOldEvents = () => axios.get(`${url}/old`);
export const fetchAPIEvents = (key) => axios.get(`${user_url}/${key}/events`);
export const createEvent = (key, newEvent) => axios.post(`${user_url}/${key}/events`, newEvent);
export const updateEvent = (key, id, updatedEvent) => axios.patch(`${user_url}/${key}/events/${id}`, updatedEvent);
export const deleteEvent = (key, id) => axios.delete(`${user_url}/${key}/events/${id}`);
export const deleteParticipant = (key, id, parts) => axios.patch(`${user_url}/${key}/events/${id}/signups`, parts);
export const participateEvent = (id, participant) => axios.post(`${url}/${id}/eventsignup`, participant);
export const subscribe = (email) => axios.post(`${sub_url}/subscribe`, email);
export const confirmSignup = (id, token) => axios.post(`${validate_url}/signup/${id}`, {key: token});
export const confirmUnsub = (token, email) => axios.post(`${validate_url}/unsub/${token}`);

export const postNews = (key, data) => axios.post(`${user_url}/${key}/news`, data) 
export const getNews = (key) => axios.get(`${user_url}/${key}/news`) 