import { FETCH_ALL, FETCH_OLD, CREATE, UPDATE, DELETE, DELETE_PART } from '../constants/actionTypes'
import * as api from '../api';


// Action Creators

export const getEvents = () => async (dispatch) => {

    try {
        const { data } = await api.fetchEvents();
        dispatch({ type: FETCH_ALL, payload: data });
    } catch (error) {
        console.log(error.message);
    }
}

export const getOldEvents = () => async (dispatch) => {

    try {
        const { data } = await api.fetchOldEvents();
        dispatch({ type: FETCH_OLD, payload: data });
    } catch (error) {
        console.log(error.message);
    }
}


export const getAPIEvents = (key, callback) => async (dispatch) => {

    try {
        const { data } = await api.fetchAPIEvents(key);
        
        dispatch({ type: FETCH_ALL, payload: data });
        callback({status: 200});
    } catch (error) {
        callback({status: 401});
        console.log(error.message);
    }
}

export const createEvent = (key, event) => async (dispatch) => {
    try {
        const { data } = await api.createEvent(key, event);
        dispatch({ type: CREATE, payload: data });
    } catch (error) {
        console.log(error.message);
    }
}

export const updateEvent = (key, id, event) => async (dispatch) => {
    try {
        const { data } = await api.updateEvent(key, id, event);

        dispatch({ type: UPDATE, payload: data });
    } catch (error) {
        console.log(error.message);
    }
}

export const deleteEvent = (key, id) => async (dispatch) => {
    try {
        await api.deleteEvent(key, id);

        dispatch({ type: DELETE, payload: id });
    } catch (error) {
        console.log(error.message);
    }
}

export const deleteParticipant = (key, id, part, callback) => async (dispatch) => {
    try {
        const res = await api.deleteParticipant(key, id, part);
        console.log(res.status);
        dispatch({ type: DELETE_PART, payload: {event: id, parts: part }});

    } catch (error) {
        console.log(error.message);
    }
}

export const participateEvent = (id, participant, callback) => async (dispatch) => {
    try {
        const res = await api.participateEvent(id, participant);

        callback({status: res.status, message: res.data.message});
    } catch (error) {
        callback({status: error.response.status, message: error.response.data.message});
    }
}

export const subscribe = (email, visible, callback) => async (dispatch) => {
    try {
        const { status } = await api.subscribe({email: email, visible: visible});
        callback(status);
    } catch (error) {
        if (error.response) {
            callback(error.response.status);
        } else if (error.request) {
            callback(400);
        }
    }
}

export const unsubscribe = (email, callback) => async (dispatch) => {
    try {
        const { status } = await api.subscribe({email: email});
        callback(status);
    } catch (error) {
        if (error.response) {
            callback(error.response.status);
        } else if (error.request) {
            callback(400);
        }
    }
}